	module.exports = {"data":{"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"nodes":[{"node":{"frontmatter":{"title":"Family","tags":["family","personal"],"cover":"https://unsplash.it/1152/300/?random?Family","date":"2015-01-11T16:06","author":"eddie"},"fields":{"slug":"/family"},"html":"<p>Great photo of Lauren and Ethan</p>\n<p><img src=\"/images/lauren-ethan.jpg\" alt=\"Ethan and Lauren\"></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"This will become a toy","tags":["star wars"],"cover":"https://unsplash.it/1152/300/?random?BB8","date":"2014-11-29T10:38","author":"eddie"},"fields":{"slug":"/this-will-become-a-toy"},"html":"<p><img src=\"/images/soccer-ball-droid.jpg\" alt=\"Star Wars - Soccer ball droid\"></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"5 Ways To Burn Out Programming","tags":["advice","burn out","quote post"],"cover":"https://unsplash.it/1152/300/?random?BurnOut","date":"2014-03-29T10:19","author":"eddie"},"fields":{"slug":"/5-ways-to-burn-out-programming"},"html":"<p>I've just read a great blog post by <a href=\"http://blog.braegger.pw\">Josh Braegger</a>. I highly recommend giving it a read.</p>\n<p><strong><a href=\"http://blog.braegger.pw/5-ways-to-burn-out-programming\">5 Ways To Burn Out Programming</a></strong></p>\n<p><strong>1. Think about your project and only the project</strong></p>\n<blockquote>\n<p>...You put off fun features for the sake of hitting a deadline...</p>\n</blockquote>\n<blockquote>\n<p>...you started programming because you thought it was fun, why not keep programming because it's fun? Take that little extra time to put in a feature you want...</p>\n</blockquote>\n<p><strong>2. Have a negative attitude toward everything</strong></p>\n<blockquote>\n<p>...the best projects I've worked on worked out that way because we had a great, positive team. We enjoyed showing up every day to work, told each other when we did awesome things, held back heavy-handed criticism and phrased it in a productive manner.</p>\n</blockquote>\n<p><strong>3. Use the tools you know, because you're faster that way</strong></p>\n<blockquote>\n<p>...you should prototype, play around, and become an expert in new tech...</p>\n</blockquote>\n<p><strong>4. Switch jobs often</strong></p>\n<blockquote>\n<p>...Your career stagnates, you don't develop your skills as deeply (only breadth), people don't trust you'll stay employed for a while, and you're constantly having to prove yourself...</p>\n</blockquote>\n<p><strong>5. Work long hours, ignore your life</strong></p>\n<blockquote>\n<p>...you go die-hard to meet an impossible deadline. You delivered the project on time, with all the extra features you wanted. You are the hero. High fives all around. And if you're lucky, you'll get that bonus.</p>\n</blockquote>\n<blockquote>\n<p>That's great the first time. But how about the second. And the third. It's a bomb, and you dont know how short the fuse is.</p>\n</blockquote>\n<p><small>Source <a href=\"http://blog.braegger.pw/5-ways-to-burn-out-programming\">5 Ways To Burn Out Programming</a></small></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"Good Fucking Design Advice","tags":["design","quotes","quote post"],"cover":"https://unsplash.it/1152/300/?random?Design","date":"2014-01-25T12:43","author":"eddie"},"fields":{"slug":"/design-advice"},"html":"<p><img src=\"/images/design-advice-work-harder.jpg\" alt=\"Work fucking harder.\"></p>\n<p><img src=\"/images/design-advice-love-your-job.jpg\" alt=\"Love your fucking job.\"></p>\n<p><img src=\"/images/design-advice-do-it-right.jpg\" alt=\"Do it right the first fucking time.\"></p>\n<p><img src=\"/images/design-advice-criticism.jpg\" alt=\"Learn to take some fucking criticism.\"></p>\n<p><img src=\"/images/design-advice-challange-yourself.jpg\" alt=\"Constantly fucking challenge yourself.\"></p>\n<p><img src=\"/images/design-advice-helvetica.jpg\" alt=\"Use fucking Helvetica.\"></p>\n<p><img src=\"/images/design-advice-get-over-yourself.jpg\" alt=\"Get over your fucking self.\"></p>\n<p><img src=\"/images/design-advice-promote.jpg\" alt=\"Promote your fucking self.\"></p>\n<p><img src=\"/images/design-advice-give-a-damn.jpg\" alt=\"Give a fucking damn.\"></p>\n<p><img src=\"/images/design-advice-never-assume.jpg\" alt=\"Never assume fucking anything.\"></p>\n<p><small>Source <a href=\"http://goodfuckingdesignadvice.com\">goodfuckingdesignadvice.com</a></small></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"Had a great birthday with my son","tags":["family","personal"],"cover":"https://unsplash.it/1152/300/?random?Tigger","date":"2013-10-20T22:36","author":"eddie"},"fields":{"slug":"/had-a-great-birthday-with-my-son"},"html":"<p><img src=\"/images/eddie-ethan-tigger.jpg\" alt=\"Ethan and me\"></p>","timeToRead":1}}],"page":8,"pages":9,"total":45,"limit":5,"prev":"/7","next":"/9"}}

